import { Component, h, Prop, Element, State, Listen } from '@stencil/core';

@Component({
  tag: 'ks-order-progress',
})
export class OrderProgress {

  @Prop() current: number;
  @Prop() cart: string = 'Koszyk';
  @Prop() shipping: string = 'Dane do wys≈Çki';
  @Prop() confirmation: string = 'Potwierdzenie';

  @Element() root: HTMLElement;

  componentDidLoad(){
    this.root.style.display = "block";
    this.root.style.width = "100%";
  }

  @State() mobile: boolean = false;

  @Listen('resize', { target: 'window' })
  resizeHandler() {
    this.mobile = window.innerWidth < 800 ? true : false;
  }

  componentWillLoad() {
      this.resizeHandler();
  }

  StepStyle(step: number) : {[key: string]: string} {
    const tile = {
      padding: this.mobile ? "20px" : "30px",
      backgroundColor: "#252525",
      color: "white",
      borderLeft: "1px solid #404040"
    }

    const active = { ...tile,
        backgroundColor: "#404040",
        borderLeft: "1px solid #404040"
    }


    if(this.mobile) return tile;

    if(step == this.current) return active;
    else return tile;
  }

  render() {
    return[
            <div class={"uk-text-center uk-flex uk-flex-evenly ks-text-decorated " + (this.mobile ? "uk-child-width-1-1" : "uk-child-width-1-3")}
                 style={{fontSize: "16px", backgroundColor: "#252525"}}>
              {!this.mobile || this.current == 0 ?
                <div style={this.StepStyle(0)}>
                  <span uk-icon="icon: cart; ratio: 1.2" style={{marginRight: "5px", transform: "translateY(-3px)"}}></span> {this.cart}
                </div>
              : null}

              {!this.mobile || this.current == 1 ?
                <div style={this.StepStyle(1)}>
                  <span uk-icon="icon: user; ratio: 1.2" style={{marginRight: "5px", transform: "translateY(-2px)"}}></span> {this.shipping}
                </div>
              : null}

              {!this.mobile || this.current == 2 ?
                <div style={this.StepStyle(2)}>
                  <span uk-icon="icon: check; ratio: 1.2" style={{marginRight: "5px", transform: "translateY(-1px)"}}></span> {this.confirmation}
                </div>
              : null}
            </div>
    ];
  }
}